.story {
    display: flex;
    align-items: center;
    margin-bottom: calc(100vw / 25 * 2);

}

.img-story--3-4 {
    height: 39vw;
}

@media (max-width: 767px) {
    .story {
        flex-direction: column!important;
        margin-bottom: calc(100vw / 25 * 4);
    }
    .img-story--3-4 {
        height: 100vw;
        margin-bottom: 20px;
    }
}