
.visual-story {
    display: flex;
}

.visual-one {
    display: flex;
    height: 62vw;
}

.visual-two {
    display: flex;
    height: 36vw;
}

.paralax {
    display: flex;
    flex-direction: column;
}

@media (max-width: 767px) {
    .visual-story {
        justify-content: flex-start;
        flex-direction: column-reverse!important;
    }
    .visual-one {
        display: flex;
        width: 92vw;
        height: 92vw;
    }
    .visual-two {
        height: 92vw;
    }

    .paralax {
        flex-direction: column-reverse;
    }
}

.sticky {
    position: sticky;
    top: 85px;
}