body,
html {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 10px;
    line-height: 1.6;
    background-color: #ffffff;
    color: #292929;
    -webkit-font-smoothing: antialiased;

}
.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.hidden {
    opacity: 0;
    transition: opacity ease-out 0.8s;
}

.hidden2 {
    transform: scale(1.1);
    transition: all ease-out 0.8s;
}

.reveal {
    opacity: 1;
}

.reveal2 {
    transform: scale(1);
}

.cover {
    object-fit: cover;
    width: 100%;
    height: 100%;

}

.btn {
    margin-top: 20px;
    font-size: 1.6rem;
    background-color: #292929;
    color: #ffffff;
    padding: 1rem 3rem 1.1rem;
    border-radius: 2.2rem;
}

/* Navbar */

.navbar2 {
    z-index: 1;
    color: #ffffff;
    display: flex;
    position: sticky;
    width: calc(100% - 6rem);
    top: 56px;
    padding: 0 3rem;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    font-size: 1.8rem;
    mix-blend-mode: exclusion;
    pointer-events: none;
}
.navbar2-menu {
    display: flex;
    width: 16px;
    justify-content: space-between;
    mix-blend-mode: difference;

}

.dot {
    width: 3px;
    height: 3px;
    border-radius: 2px;
    background-color: #ffffff;
}

.password {
    display: flex;
    width: 100%;
    height: 100vh;
    background-color: $codgray;
    justify-content: center;
    align-items: flex-start;

    div {
        height: 100%;
    }

    form {
        width: 100%;
        position: relative;
    }

    input {
        flex: 1 1 auto;
        width: 100%;
        height: 50px;
        background-color: transparent;
        padding: 0 30px;
        border: 1px solid rgba(255,255,255, .2);
        border-radius: 25px;
        font-size: 1.6rem;
        color: $white;
        transition: all ease-in-out .3s;
        &:focus {
            outline: none;
            border-color: rgba(255,255,255, .5);

            
        }

        &::placeholder {
            color: rgba(255,255,255, .3);
        }
        &::-webkit-credentials-auto-fill-button {
            background-color: transparent!important;
        }
    }

    button {
        position: absolute;
        right: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: -80px;
        width: 80px;
        height: 50px;
        font-size: 1.6rem;
        border: none;
        cursor: pointer;
        border-radius: 25px;
        background-color: transparent;
        transition: all ease-in-out 0.3s;


        img {
            opacity: .5;
            transition: opacity ease-in-out .3s;
            
        }

        &:hover {
            img {
                opacity: 1;
            }
        }
    }

}

.error-message {
    position: absolute;
    width: 100%;
    margin-left: 25px;
    top: 55px;
    font-size: 1.3rem;
    color: $alizarincrimson;
}
