.logo {
    display: flex;
    align-items: center;
    justify-content: center;

    .logo1, .logo2 {
        height: 23px;
    }
}

.mix {
    mix-blend-mode: difference;
}


.Arched_x0020_Green {
    fill: url(#SVGID_1_);
    stroke: #FFFFFF;
    stroke-width: 0.25;
    stroke-miterlimit: 1;
}

.st0 {
    fill: inherit;
}

.st1 {
    fill: #ffffff;
}

.st2 {
    fill: transparent;
}

.st1bis {
    fill: #00552D;
    transition: all ease-out .2s;
}

.st2bis {
    fill: #E10041;
    transition: all ease-out .2s;
}

.navbar {
    height: 85px;
    position: sticky;
    display: flex;
    margin-top: 56px;
    top: 0;
    right: 0;
    left: 0;
    background-color: transparent;
    color: #ffffff;
    fill: #ffffff;
    transition: all ease-out .2s;
}

.bg-white {
    background-color: white;
    color: #292929;
    fill: #292929;
    transition: all ease-out .2s;
}

.fixed {
    position: fixed;
}

.hiden {
    height: 0;
    opacity: 0;
}



.navigation {
    display: flex;
    list-style: none;
    padding: 0;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.navlink {
    font-size: 1.5rem;
    line-height: 2.5rem;

    a {
        text-decoration: none;
        color: inherit;
        padding: 10px 0;
        border-bottom: 2px solid transparent;
        transition: all ease-out .3s;

        &:hover {
            border-color: #E5E5E5;
        }
    }
}


.img-responsive {
    display: flex;
    width: 100%;
    max-height: 100%;
}

.search {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.basket,
.menu {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.basket:before {
    position: absolute;
    content: '';
    width: 1px;
    height: 25px;
    background-color: #ffffff;
    left: 25%;
}
.grey {
    background-color: #E5E5E5;
}

.account {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .icon {
        margin-left: 20px;
    }
}

.icon {
    width: 25px;
    height: 25px;
}

@media (max-width: $bp-tablet) {

    .main {
        justify-content: center;
    }

    .search {
        order: -1;
        justify-content: flex-start;
    }

    .main-menu,
    .account {
        display: none;
    }

    .logo1 {
        display: none;
    }

    .basket:before {
        display: none;
    }

    .basket .icon {
        margin-bottom: 2px;
    }

    .navbar {
        height: 60px;
    }
    
    .header {
    padding-top: $column-width * 2;
    }

}

@media (min-width: $bp-tablet + 1) {
    .menu {
        display: none;
    }
}

.toggle {
    display: flex;
}

.hide {
    opacity: 0;
    transition: opacity 900ms 200ms cubic-bezier(0.645, 0.045, 0.355, 1);

}

.hide.isvisible{
    opacity: 1;
}


.slideshow {
    overflow-x: hidden;

}

.slide-container {
    transition: all ease .3s;
}

.arrow {
    padding: 0 20px;
}

.arrow img {
    margin: 0 5px;
}


::-webkit-scrollbar {
    display: none;
}