.banner {
    overflow-y: scroll;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    font-size: 1.6rem;
    line-height: 150%;
    color: #ffffff;
    width: 100%;
    background-color: #191919;
    transition: all ease-in-out 0.5s;
}

.inner-banner {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.inner-banner a {
    font-weight: 500;
}

.banner banner-title {

    opacity: 0.7;
}

.banner a {
    color: #ffffff!important;
    font-weight: bold;
    text-decoration: none;
    opacity: 0.7;

}

.chevron {
    display: flex;
    margin-left: 10px;
    opacity: 0.7;
    cursor: pointer;
    padding: 10px;
}

.chevron::before {
    content: '';
    width: 8px;
    height: 1px;
    border-radius: 1px;
    background-color: #ffffff;
    transform: rotateZ(45deg) translateX(1px) translateY(-1px);
}

.chevron::after {
    content: '';
    width: 8px;
    height: 1px;
    border-radius: 1px;
    background-color: #ffffff;
    transform: rotateZ(-45deg) translateX(-1px) translateY(-1px);
}

.chevron {
    animation: translation ease-in-out 1.8s infinite;
}
@keyframes translation {

    0% {
        transform: translateY(-1px);
    }
    50% {
        transform: translateY(4px);
    }
    100% {
        transform: translateY(-1px);
    }

}

.firstpart {
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    cursor: pointer;
}

.back {
    cursor: pointer;
    mix-blend-mode: difference;
    position: fixed;


    .icon {
        margin-right: 10px;
        margin-left: 10px;
        transition: transform ease-in-out .5s;
    }

    &:hover {
        .icon {
            transform: translateX(-5px);
        }
    }
}

@media (max-width: $bp-mobile) {
    .back .icon {
        margin-left: 0;
    }
}
