*,
::after,
::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.row {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}
@for $i from 1 through 20 {
    .z#{$i} {
        z-index: $i;
    }
}
@for $i from 1 through $column-number {
    .col-#{$i} {
        display: flex;
        flex-direction: row;
        width: $i * $column-width;
    }
}
@for $i from 0 to $column-number {
    .offset-#{$i} {
        margin-left: $i * $column-width;
    }

    .pl-#{$i} {
        padding-left: $i * $column-width;
    }
    .pr-#{$i} {
        padding-right: $i * $column-width;
    }
}
@media (max-width: $bp-tablet) {
     @for $i from 1 through $column-number{
        .col-m-#{$i} {
            display: flex;
            flex-direction: row;
            width: $i * $column-width;
        }
    }
    @for $i from 0 to $column-number {
        .offset-m-#{$i} {
            margin-left: $i * $column-width;
        }

        .pl-m-#{$i} {
            padding-left: $i * $column-width;
        }
        .pr-m-#{$i} {
            padding-right: $i * $column-width;
        }
    }

    .flex-m-reverse {
        display: flex;
        flex-direction: row-reverse;
    }
    .flex-m-col {
        flex-direction: column;
    }
    .flex-m-col-reverse {
        flex-direction: column-reverse;
    }
}
@for $i from 1 through 20 {
    .mgt-#{$i} {
        margin-top: $i * $column-width;
    }
    .mgbt-#{$i} {
        margin-bottom: $i * $column-width;
    }
}
@media (max-width: $bp-mobile) {
     @for $i from 1 through $column-number{
        .col-s-#{$i} {
            display: flex;
            flex-direction: row;
            width: $i * $column-width;
        }
    }
    @for $i from 0 to $column-number {
        .offset-s-#{$i} {
            margin-left: $i * $column-width;
        }
        .pl-s-#{$i} {
            padding-left: $i * $column-width;
        }
        .pr-s-#{$i} {
            padding-right: $i * $column-width;
        }
    }
    @for $i from 1 through 20 {
        .mgt-s-#{$i} {
            margin-top: $i * $column-width;
        }
        .mgbt-s-#{$i} {
            margin-bottom: $i * $column-width;
        }

    }

    .flex-s-reverse {
        display: flex;
        flex-direction: row-reverse;
    }
    .flex-s-col {
        flex-direction: column;
    }
    .flex-s-col-reverse {
        flex-direction: column-reverse;
    }
}

