

@font-face {
    font-family: 'Helvetica Neu';
    src: url('../fonts/Helvetica73BoldExtended.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}




h1, h2, h3, h4, h5, h6, p, a {
    margin: 0;
    line-height: 150%;
}

.text-xxl {
    font-weight: 600;
    font-size: 7rem;
    letter-spacing: -0.1rem;
}

.text-xl {
    font-weight: 600;
    font-size: 5rem;
}

.text-l {
    font-weight: 600;
    font-size: 2.8rem;
}

.text-m {
    font-weight: 400;
    font-size: 2.2rem;
}

.text-s {
    font-weight: 400;
    font-size: 1.6rem;
}

.text-xs {
    font-weight: 400;
    font-size: 1.3rem;
}

.text-overline {
    font-weight: 600;
    font-size: 1rem;
    letter-spacing: .6rem;
    text-transform: uppercase;
}

.text-custom {
    font-family: 'Helvetica Neu';
}

.light {
    color: $white;
}

.dark {
    color: $mineshaft;
}

.medium-light {
    color: $white;
    opacity: .5;
}

.medium-dark {
    color: $mineshaft;
    opacity: .5;
}

.text-align-left {
    text-align: left;
}

.text-align-center {
    text-align: center;
}

.text-align-right {
    text-align: right;
}

.fw-reg {
    font-weight: 400;
}

.fw-bold {
    font-weight: 600;
}

@media (max-width: $bp-mobile) {
    .text-xxl {
    font-weight: 600;
    font-size: 4rem;
    letter-spacing: -0.1rem;
}

}
